@import url(https://fonts.googleapis.com/css2?family=Lato&family=Roboto:wght@500&display=swap);
.main_img{
    float: right;
    width:700px;
    height: 500px;
}
.swiggy_logo{
    height:60px;
}

.feature-img{
    height:200px;
}
.img-play{
    padding:10px;
}

.left_img{
    height:500px;
    position:absolute;
    right:500px;

}
.right_img{
    height:500px;
    position:absolute;
    right:150px;
    margin-top:17px;
}
.footer-swiggy{
    padding:20px;
    height:42px;

}
.social-media{
    height:20px;
    padding:22px;   
}
.social-media:hover{
    background-color: red;
}

@media(max-width:1000px){
    .left_img, .right_img,.main_img{
        display: none;
    }


}

.feature-data{
    width:350px;
    text-align: center;
    margin:2px 80px;
}
.feature-heading{
    color:white;
    padding-top:24px;
    font-size:1.5rem;
    font-family: 'Lato', sans-serif;
    font-weight:650;
}
.feature-desc p{
    width:300px;
    color:#e0cbc1;
    padding:27px 10px;
    font-size:1rem;
    line-height: 1.3;
    font-weight:200;
    font-family: 'Roboto', sans-serif;
}


*{
    margin:0;
    padding:0;
}
.feature{
    background-color: #2b1e16;
    display: flex;
    
}
@media(max-width:1000px){
    .feature{
        flex-wrap: wrap;
        justify-content: center;
        padding:20px 10px;
    }
}
.info-div{
    margin-left: 6%;
    margin-top:80px;
}
.info-div h1{
    width:300px;
    font-size:2.5rem;
    font-family: 'Roboto', sans-serif;
    color:#282c3f;
    font-weight:600;
    line-height: 1.3;
    /* padding-top:22%; */
    padding-bottom:35px;   
}
.info-div p{
    width:400px;
    font-size:20px;
    font-weight:300;
    font-family: 'Lato', sans-serif;
    color:#7e808c;
    line-height:1.2;
    padding-bottom:60px;
}
.div_img{
    width:200px;
}
@media(max-width:1000px){
    .info-div p{
        width:90%;
    }
}
.pocket_section{
    display: flex;
    overflow-x:hidden;
    padding-bottom:21px;
}
.heading-div{
    color:gray;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 700;
    font-family: 'Lato', sans-serif;
    line-height: 6.4;
}
li{
    padding:0;
}
 .a{
    color:#fff;
    font-weight:100;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    line-height:2.3;
  
} 
.first_div{
    display: flex;
    border-bottom: 1px solid gray;
}
.divv{
    width:300px;
    margin:40px;
}
.footer_btn{
    width:250px;
    padding:85px 20px;
}
.footer_btn button{
    background:transparent;
    margin-top:20px;
    border:none;
}
.footer_btn  button:hover{
 border:2px solid red;
    
}

.second-div{
    padding:20px;
    border-bottom: 1px solid gray;
}
.second_div_list{
    display:flex;
}
.second_div_list ul{
    width:300px;
    margin:40px;
}
.third_div{
    display:flex;
    padding-top:30px;
}
.third_div p{
    flex-grow: 1;
    text-align: center;
    font-size: 20px;
    color:#fff;
    padding:25px;
    letter-spacing: -.6px;
}
@media(max-width:1000px){
    .first_div,.second_div_list, .third_div{
        flex-wrap: wrap;
    }
  
}

*{
    margin:0;
    padding: 0;
}
.footer_div{
    overflow-x: hidden;
    background-color:#000;
    margin-top:80px;
    padding:75px 65px;
}
@media(max-width:769px){
    .footer_div{
        padding: 20px 10px;

    }
} 
    

.login_btn{
    color:#000;
    font-size:16px;
    font-weight:600;
    border:none;
    background-color: transparent;
    padding-right:20px;
}
.login_btn:hover{
    cursor: pointer;
    color:tomato;
}
.sign_btn{
    color:#fff;
    font-size:16px;
    font-weight:600;
    background:#000;
    padding:5px 12px;
    height:40px;
    cursor: pointer;
    border: none;
    outline: none;
}
.Location_img{
    width: 16px;
}
.Location_btn{
    display: flex;
    align-items: center;
    grid-gap:3px;
    gap:3px;
    font-size: 15px !important;
    background-color: transparent;
    border: none;
    outline: none;
    padding: 10px;
}
.Location_btn:hover{
    background-color: #e6e6f0;
}
.Food_btn{
    margin: 0px !important;
    padding: 10px 20px;
    background-color: #fc8019;
    border: none;
    outline: none;
    color: #ffff;
    font-weight: 600;
}


.input_div{
    border: 1px solid coral;
    display: flex;
    justify-content: space-between;
    margin: 25px 0 0;
}
.input_div>*{
    font-size: 1.2em;
    margin: 10px 0px;
}
.input_div>input{
    border: none;
    width: 60%;
    padding: 0px 20px;
}
.input_div>input:focus{
    border: none;
    outline: none;
}
@media(max-width:720px){
    .input_div{
        flex-wrap: wrap;
    }
    .input_div>input{
        width: 100%;
    }
}
@media(max-width:650px){
    .input_div{
        width: 70%;
    }   
}
*{
    margin:0;
    padding:0;
}
.whole-body
{
    display: flex;
    grid-gap: 40px;
    gap: 40px;
    text-align:Center;
    align-items: center;
    justify-content: space-between;
    overflow-x: hidden;
}
.left-comp{
    padding-left: 50px;
    flex: auto;
}


.lc-first{
    display: flex;
    padding: 20px 0px;
    text-align: center;
    justify-content: space-between;
}
.main_btn{
    width:300px;
    padding:20px;
    margin-right:-80px;
}
.main-header{
    margin-top:20px;
    position:relative;
    left:-75px;
}
.main-header h1{
    width:300px;
    font-size:40px;
    font-family: 'Roboto', sans-serif;
    color:#282c3f;
    font-weight:600;
    line-height: 1.3;
    padding-top:10px;
    padding-bottom:15px;  
}
.main-header p{
    width:600px;
    font-size:21px;
    font-weight:300;
    font-family: 'Lato', sans-serif;
    color:#7e808c;
    line-height:1.7;
    padding-top:5px;
}
@media(max-width:1000px){
    .whole-body{
        margin:0;
        padding:10px 20px 20px;
    }
    .right-comp{
        display: none;
    }
}
@media(max-width:720px){
    .lc-first{
        flex-wrap: wrap;
    }
    .left-comp{
        padding: 0;
    }
}
@media(max-width:650px){
    .lc-first{
        width:80%;
    }
    .whole-body{
        text-align: center;
    }
}






